
<template>
	<div class="app-contain" :style='{"minHeight":"100vh","padding":"0","margin":"20px auto 60px","borderRadius":"20px","background":"none","width":"1200px","position":"relative","height":"100%"}'>
		<div class="bread_view">
			<el-breadcrumb separator="/" class="breadcrumb">
				<el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }">首页</el-breadcrumb-item>
				<el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="24">
					<el-form-item label="音乐类型" prop="yinleleixing">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.yinleleixing?true:false"
							v-model="form.yinleleixing" 
							placeholder="请选择音乐类型"
							style="width:100%;"
							>
							<el-option v-for="(item,index) in yinleleixingLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="标签" prop="biaoqian">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.biaoqian?true:false"
							v-model="form.biaoqian" 
							placeholder="请选择标签"
							style="width:100%;"
							>
							<el-option v-for="(item,index) in biaoqianLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="歌曲封面" prop="gequfengmian">
						<uploads
							:disabled="!isAdd||disabledForm.gequfengmian?true:false"
							action="file/upload" 
							tip="请上传歌曲封面" 
							:limit="3" 
							style="width: 100%;text-align: left;"
							:fileUrls="form.gequfengmian?form.gequfengmian:''" 
							@change="gequfengmianUploadSuccess">
						</uploads>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="语言类别" prop="yuyanleibie">
						<el-input class="list_inp" v-model="form.yuyanleibie" placeholder="语言类别"
							 type="text" 							:readonly="!isAdd||disabledForm.yuyanleibie?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="上架时间" prop="shangjiashijian">
						<el-date-picker
							class="list_date"
							v-model="form.shangjiashijian"
							format="YYYY 年 MM 月 DD 日"
							value-format="YYYY-MM-DD"
							type="datetime"
							:readonly="!isAdd||disabledForm.shangjiashijian?true:false"
							placeholder="请选择上架时间"
							style="width: 100%;" />
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="歌名" prop="songname">
						<el-input class="list_inp" v-model="form.songname" placeholder="歌名"
							 type="text" 							:readonly="!isAdd||disabledForm.songname?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="音乐文件" prop="songfile">
						<uploads
							:disabled="!isAdd||disabledForm.songfile?true:false"
							type="file"
							action="file/upload" 
							tip="请上传音乐文件" 
							:limit="1" 
							style="width: 100%;text-align: left;"
							:fileUrls="form.songfile?form.songfile:''" 
							@change="songfileUploadSuccess">
						</uploads>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="歌手" prop="singer">
						<el-input class="list_inp" v-model="form.singer" placeholder="歌手"
							 type="text" 							:readonly="!isAdd||disabledForm.singer?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="作词" prop="zuoci">
						<el-input class="list_inp" v-model="form.zuoci" placeholder="作词"
							 type="text" 							:readonly="!isAdd||disabledForm.zuoci?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="作曲" prop="zuoqu">
						<el-input class="list_inp" v-model="form.zuoqu" placeholder="作曲"
							 type="text" 							:readonly="!isAdd||disabledForm.zuoqu?true:false" />
					</el-form-item>
				</el-col>

			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick">取消</el-button>
				<el-button class="formModel_confirm" @click="save"
					type="success"
					>
					保存
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//基础信息
	const tableName = 'yinlexinxi'
	const formName = '音乐信息'
	//基础信息
	const breadList = ref([{
		name: formName
	}])
	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form表单
	const form = ref({
		yinleleixing: '',
		biaoqian: '',
		gequfengmian: '',
		yuyanleibie: '',
		shangjiashijian: '',
		songname: '',
		songfile: '',
		singer: '',
		storeupnum: '0',
		clicktime: '',
		clicknum: '0',
		zuoci: '',
		zuoqu: '',
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		yinleleixing : false,
		biaoqian : false,
		gequfengmian : false,
		yuyanleibie : false,
		shangjiashijian : false,
		songname : false,
		songfile : false,
		singer : false,
		storeupnum : false,
		clicktime : false,
		clicknum : false,
		zuoci : false,
		zuoqu : false,
	})
	const isAdd = ref(false)
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		yinleleixing: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		biaoqian: [
		],
		gequfengmian: [
		],
		yuyanleibie: [
		],
		shangjiashijian: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		songname: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		songfile: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		singer: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		storeupnum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		clicktime: [
		],
		clicknum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		zuoci: [
		],
		zuoqu: [
		],
	})
	//音乐类型列表
	const yinleleixingLists = ref([])
	//标签列表
	const biaoqianLists = ref([])
	//歌曲封面上传回调
	const gequfengmianUploadSuccess=(e)=>{
		form.value.gequfengmian = e
	}
	//音乐文件上传回调
	const songfileUploadSuccess=(e)=>{
		form.value.songfile = e
	}
	//methods

	//methods
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='yinleleixing'){
					form.value.yinleleixing = row[x];
					disabledForm.value.yinleleixing = true;
					continue;
				}
				if(x=='biaoqian'){
					form.value.biaoqian = row[x];
					disabledForm.value.biaoqian = true;
					continue;
				}
				if(x=='gequfengmian'){
					form.value.gequfengmian = row[x];
					disabledForm.value.gequfengmian = true;
					continue;
				}
				if(x=='yuyanleibie'){
					form.value.yuyanleibie = row[x];
					disabledForm.value.yuyanleibie = true;
					continue;
				}
				if(x=='shangjiashijian'){
					form.value.shangjiashijian = row[x];
					disabledForm.value.shangjiashijian = true;
					continue;
				}
				if(x=='songname'){
					form.value.songname = row[x];
					disabledForm.value.songname = true;
					continue;
				}
				if(x=='songfile'){
					form.value.songfile = row[x];
					disabledForm.value.songfile = true;
					continue;
				}
				if(x=='singer'){
					form.value.singer = row[x];
					disabledForm.value.singer = true;
					continue;
				}
				if(x=='storeupnum'){
					form.value.storeupnum = row[x];
					disabledForm.value.storeupnum = true;
					continue;
				}
				if(x=='clicktime'){
					form.value.clicktime = row[x];
					disabledForm.value.clicktime = true;
					continue;
				}
				if(x=='clicknum'){
					form.value.clicknum = row[x];
					disabledForm.value.clicknum = true;
					continue;
				}
				if(x=='zuoci'){
					form.value.zuoci = row[x];
					disabledForm.value.zuoci = true;
					continue;
				}
				if(x=='zuoqu'){
					form.value.zuoqu = row[x];
					disabledForm.value.zuoqu = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.storeupnum='0'
			form.value.clicknum='0'
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
		})
		context?.$http({
			url: `option/yinleleixing/yinleleixing`,
			method: 'get'
		}).then(res=>{
			yinleleixingLists.value = res.data.data
		})
		context?.$http({
			url: `option/biaoqian/biaoqian`,
			method: 'get'
		}).then(res=>{
			biaoqianLists.value = res.data.data
		})
	}
	//初始化
	//取消
	const backClick = () => {
		history.back()
	}
	//提交
	const save=()=>{
		if(form.value.gequfengmian!=null) {
			form.value.gequfengmian = form.value.gequfengmian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		if(form.value.songfile!=null) {
			form.value.songfile = form.value.songfile.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									history.back()
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							history.back()
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
	
</script>
<style lang="scss" scoped>
	// 面包屑盒子
	.bread_view {
		border-radius: 0px;
		padding: 0px 20px 10px;
		margin: 0px auto;
		background: none;
		width: calc(100% - 0px);
		border-color: #66666650;
		border-width: 0 0 1px;
		position: relative;
		border-style: solid;
		:deep(.breadcrumb) {
			font-size: 14px;
			line-height: 1;
			.el-breadcrumb__separator {
				margin: 0 9px;
				color: #333;
				font-weight: 500;
			}
			.first_breadcrumb {
				.el-breadcrumb__inner {
					color: #333;
					display: inline-block;
				}
			}
			.second_breadcrumb {
				.el-breadcrumb__inner {
					color: #333;
					display: inline-block;
				}
			}
		}
	}
	// 表单
	.add_form{
		border: 0px solid #27bacc30;
		border-radius: 0px;
		padding: 30px 7%;
		background: none;
		// form item
		:deep(.el-form-item) {
			border: 0px solid #eee;
			padding: 6px 0;
			margin: 0 0 20px 0;
			background: none;
			display: flex;
			//label
			.el-form-item__label {
			 background: none;
			 display: block;
			 width: auto;
			 min-width: 150px;
			 text-align: right;
			}
			// 内容盒子
			.el-form-item__content {
				display: flex;
				width: calc(100% - 150px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 输入框
				.list_inp {
					padding: 0 10px;
					background: #fff;
					width: 50%;
					border-color: #ddd;
					border-width: 1px;
					line-height: 36px;
					box-sizing: border-box;
					border-style: solid;
					min-width: 350px;
					height: 36px;
					//去掉默认样式
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				//日期选择器
				.list_date {
					border-radius: 0px;
					background: #fff;
					width: 50%;
					border-color: #ddd;
					border-width: 1px;
					line-height: 36px;
					box-sizing: border-box;
					border-style: solid;
					min-width: 300px;
					//去掉默认样式
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
					}
				}
				// 下拉框
				.list_sel {
					border-radius: 0px;
					padding: 0 10px;
					background: #fff;
					width: 50%;
					border-color: #ddd;
					border-width: 1px;
					line-height: 36px;
					box-sizing: border-box;
					border-style: solid;
					min-width: 300px;
					//去掉默认样式
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
							.el-input__wrapper{
								border: none;
								box-shadow: none;
								background: none;
								border-radius: 0;
								height: 100%;
								padding: 0;
							}
							.is-focus {
								box-shadow: none !important;
							}
						}
					}
				}
				//图片上传样式
				.el-upload-list  {
					//提示语
					.el-upload__tip {
						margin: 7px 0 0;
						color: #999;
						display: flex;
						font-size: 14px;
						justify-content: flex-start;
						align-items: center;
					}
					//外部盒子
					.el-upload--picture-card {
						border: 1px solid #ddd;
						cursor: pointer;
						border-radius: 2px;
						background: #fff;
						width: 120px;
						line-height: 90px;
						text-align: center;
						height: 80px;
						//图标
						.el-icon{
							color: #999;
							font-size: 24px;
						}
					}
					.el-upload-list__item {
